.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-btn {
  flex: 1;
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.prev-btn,
.next-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  text-transform: capitalize;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s linear;
}

.newPage-btn {
  flex: 1;
  display: flex;
}

.newPage {
  flex: 1;
  border-color: transparent;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s linear;
}
.newPage:hover,
.activePage:hover,
.prev-btn:hover,
.next-btn:hover {
  color: #007bff;
}
.activePage {
  color: #007bff;
}
@media screen and (max-width: 675px) {
  .page-container {
    flex: 0 0 45px;
    padding: 0.15rem 1rem;
    margin-bottom: 0.25rem;
  }

  .prev-btn,
  .next-btn {
    display: none;
  }
  .newPage {
    font-size: 1rem;
  }
}

@media screen and (min-width: 675.01px) and (max-width: 900px) {
  .page-container {
    flex: 0 0 47.5px;
    padding: 0.25rem 1rem;
    margin-top: 0.25rem;
    margin-bottom: 0.25rem;
  }

  .prev-btn,
  .next-btn {
    font-size: 1.1rem;
  }
  .newPage {
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 900.01px) {
  .page-container {
    flex: 0 0 50px;
    padding: 0.25rem 1rem;
    margin-top: 0.25rem;
    margin-bottom: 0.25rem;
  }

  .prev-btn,
  .next-btn {
    flex: 0 0 100px;
    font-size: 1.2rem;
  }
  .newPage-btn {
    justify-content: space-evenly;
  }
  .newPage {
    flex: 0 70px;
    font-size: 1.2rem;
  }
}
