.section-search {
  flex: 0 0 50px;
}

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95vw;
  margin: 0 auto;
  box-shadow: 2px 3px 2px 0px rgba(0, 0, 0, 0.4);
  border-radius: 0.25rem;
}
.form-control {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
}

.form-control input {
  flex: 1;
  height: 100%;
  padding: 1rem;
  background-color: transparent;
  border-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  color: #fff;
  text-transform: capitalize;
  font-size: 1rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  transition: all 0.3s linear;
}
.form-control input:hover {
  border: 1px solid rgba(255, 255, 255, 1);
}
.form-control input::placeholder {
  color: #fff;
}
.search-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-left: 0.25rem;
  background-color: transparent;
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 0.3s linear;
}
.search-btn:hover {
  border: 1px solid rgba(255, 255, 255, 1);
}
@media screen and (max-width: 675px) {
  .search-form {
    /* padding: 0.35rem 1rem; */
    padding: 0.35rem 0;
    margin: 0.125rem auto;
    /* border: 1px solid red; */
  }
  .search-btn {
    flex: 0 0 40px;
    font-size: 1rem;
  }
}

@media screen and (min-width: 675.01px) and (max-width: 900px) {
  .search-form {
    padding: 0.35rem 0;
    margin: 0.25rem auto;
    /* border: 1px solid red; */
  }
  .search-btn {
    flex: 0 0 50px;
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 900.01px) {
  .search-form {
    padding: 0.35rem 0;
    margin: 0.35rem auto;
  }
  .search-btn {
    flex: 0 0 60px;
    font-size: 1.1rem;
  }
}
