.section-movieslist {
  display: flex;
  flex-direction: column;
}
.movies-container {
  display: grid;
  background-color: #000;
}

.section-title {
  width: 95vw;
  font-size: 2rem;
  font-weight: bold;
  margin: 1.5rem auto 0.8rem;
  padding: 0.5rem;
  text-transform: capitalize;
  letter-spacing: 0.25rem;
  color: #fff;
  text-align: center;
}
@media screen and (max-width: 675px) {
  .movies-container {
    grid-auto-rows: minmax(300px, 340px);
    /* padding: 0 0.5rem; */
    /* padding: 0 1rem; */
    /* padding: 0 0.25rem; */

    /**/
    /* padding: 0 1rem; */

    gap: 0.5rem;
    /* border: 1px solid red; */
  }
}

@media screen and (min-width: 675.01px) and (max-width: 900px) {
  .movies-container {
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    /* grid-auto-rows: 410px; */
    grid-auto-rows: 370px;
    gap: 0.5rem;
  }
}

@media screen and (min-width: 900.01px) {
  .movies-container {
    grid-template-columns: repeat(auto-fill, minmax(265px, 1fr));
    grid-auto-rows: minmax(370px, 420px);
    gap: 0.35rem;
  }
}
