.movie {
  display: flex;
  flex-direction: column;
  border-radius: 0.5rem;
  padding: 0.1rem;
  background-color: #000;
  border: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 0.3s linear;
}
.movie:hover {
  border: 1px solid rgba(255, 255, 255, 1);
}
.movie-center {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 0.25rem;
  cursor: pointer;
}
.movie-img {
  width: 100%;
  height: 100%;
  border-radius: 0.25rem;
  object-fit: fill;
}
.movie-footer {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #000;
}
.movie-footer > h3 {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: capitalize;
  color: #fff;
}

.btn-details {
  display: flex;
  justify-content: center;
  border-radius: 0.25rem;
  text-transform: capitalize;
  color: #000;
  color: #fff;
  background-color: #000;
  border: 1px solid rgba(255, 255, 255, 0.2);
  letter-spacing: 0.2rem;
  cursor: pointer;
  transition: all 0.3s linear;
}
.btn-details:hover {
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 1);
  box-shadow: 0 2px 2px rgba(255, 255, 255, 0.3);
}

@media screen and (max-width: 675px) {
  .movie-center {
    flex: 0 0 60%;
    padding: 0.35rem;
    /* border: 1px solid red; */
  }
  .movie-img {
    max-width: 500px;
    width: 95%;
    /* border: 1px solid red; */
  }
  .movie-footer {
    padding: 0.35rem;
  }
  .movie-footer > h3 {
    justify-content: center;
    align-items: center;
    font-size: 1.1rem;
    text-align: center;
  }
  .footer p {
    margin: 0 0.35rem;
    /* padding: 0.35rem 0.5rem; */
    padding: 0.35rem 1rem;
    font-size: 1rem;
    /* border: 1px solid red; */
  }
  .btn-details {
    margin-top: 0.35rem;
    padding: 0.35rem 1rem;
    /* margin: 0 0.5rem; */
    margin: 0 1rem;
    /* border: 1px solid red; */
  }
}

@media screen and (min-width: 675.01px) and (max-width: 900px) {
  .movie-center {
    flex: 0 0 57%;
    padding: 0.3rem;
  }
  .movie-footer {
    padding: 0.3rem;
    margin: 0 0.3rem;
  }
  .movie-footer > h3 {
    font-size: 1.125rem;
  }
  .footer p {
    font-size: 1rem;
    padding: 0.25rem 0;
  }
  .btn-details {
    margin-top: 0.3rem;
    padding: 0.35rem 1rem;
  }
}
@media screen and (min-width: 900.01px) {
  .movie-center {
    flex: 0 0 55%;
    padding: 0.25rem;
  }
  .movie-footer {
    padding: 0.25rem;
    margin: 0.25rem;
  }
  .movie-footer > h3 {
    font-size: 1.2rem;
  }
  .footer p {
    font-size: 1.1rem;
  }
  .btn-details {
    margin-top: 0.3rem;
    padding: 0.5rem 1rem;
  }
}
